	methods: {
		receiveLua: function(event){
			if(!event || !event.data)
				return;
			
			const lua = event.data;
			
			this.NuiOpen = lua.NuiOpen;
			if(lua.NuiOpen){
				this.conditions = lua.conditions_m;
				this.body = lua.body;
				this.position_nui = 'left: '+lua.x+'; top: '+lua.y+'; transform: translate(-'+lua.x+', -'+lua.y+') scale('+ lua.scale +');';
				this.cause_death = lua.cause_death;
				this.punch = lua.punch;
				this.shots = lua.shots;
				this.cuts = lua.cuts;
				this.bruises = lua.bruises;
				this.texts = lua.texts_nui;

				if (!this.show_time)
					this.ChangeConditions();
			}
		},

		ChangeConditions() {
			setTimeout(function() {   
				if (this.NuiOpen) { 
					if(this.show_bruised) this.show_bruised = false;
					else this.show_bruised = true; 

					this.show_time = true;
					this.ChangeConditions();    
				} else {
					this.show_time = false;
				}
			}.bind(this), 1500)
		},
    },

	created: function(){
        window.addEventListener('message', this.receiveLua);  
    },
    destroyed: function(){
        window.removeEventListener('message', this.receiveLua);
    },
}
